@use 'sass:math';
@media screen and (max-width: 768px) {
  .market-tooltip-overlay .ant-tooltip-inner {
    font-size: 12px;
    padding: 12px;
    box-shadow: 0px 10px 20px rgb(0 0 0 / 10%);
    min-width: unset;

    .with-point {
      background: url(../../images/v2/market/point.svg) left 7px no-repeat;
      background-size: 4px;
      padding-left: 12px;
    }
  }

  #root {
    .all-market {
      font-family: 'PingFang SC';
      font-style: normal;
      font-weight: 600;
      font-size: 14px;
      line-height: 20px;
      color: #fff;
      padding-left: 15px;
    }

    .msl-title {
      margin: 20px 0 10px;
    }

    .w-100 {
      width: 100%;
    }
    .footer-container {
      margin: 30px 0 0;
      padding-bottom: 49px;
      color: #fff;
      a {
        color: #4950d4;
      }
      .line {
        color: rgba(84, 86, 105, 0.2);
        margin: 0 12px;
      }
    }
  }

  #root .j-wrapper {
    .sort-select {
      position: relative;
      min-width: 100px;
      &.en {
        min-width: 124px;
      }
      border-radius: 2px;
      .ant-select-selector {
        height: 20px !important;
        padding-left: 28px !important;
        padding-right: 26px !important;
        background: rgba(255, 255, 255, 0.1) !important;
        border-radius: 2px !important;
        border: none !important;
      }
      .ant-select-selection-search {
        .ant-select-selection-search-input {
          display: none !important;
        }
      }
      .ant-select-selection-item {
        line-height: 20px !important;
        padding-right: 0 !important;
        font-family: 'DM Sans';
        font-style: normal;
        font-weight: 400;
        font-size: 12px;
        line-height: 16px;
        color: #fff;
        text-overflow: unset;
      }
      .ant-select-arrow {
        margin-top: -3px;
        right: 9px;
      }
      .ant-select-suffix {
        width: 10px;
        height: 6px;
        background: url(../../images/mobile/arrow-down-white.svg) 0 0 no-repeat;
        background-size: 100%;
        > svg {
          display: none;
        }
      }
      &::after {
        content: '';
        display: block;
        width: 11px;
        height: 9px;
        background: url(../../images/mobile/sort-icon-white.svg) 0 0 no-repeat;
        background-size: 100%;
        position: absolute;
        top: 6px;
        left: 10px;
      }
      &.ant-select-open {
        background: rgba(61, 86, 214, 0.1);
        .ant-select-selection-item {
          color: #3d56d6 !important;
        }
        .ant-select-suffix {
          background-image: url(../../images/mobile/arrow-down-blue.svg);
        }
        &::after {
          background-image: url(../../images/mobile/sort-active-icon.svg);
        }
      }
      &.desc {
        &::after {
          background-image: url(../../images/mobile/black-theme-default-down.svg);
        }
        &.ant-select-open {
          &::after {
            background-image: url(../../images/mobile/black-theme-active-down.svg);
          }
        }
      }
      &.asc {
        &::after {
          background-image: url(../../images/mobile/black-theme-default-top.svg);
        }
        &.ant-select-open {
          &::after {
            background-image: url(../../images/mobile/black-theme-active-top.svg);
          }
        }
      }
    }
  }

  #root .bs-v2-list {
    .ant-table-tbody > tr:nth-child(2) {
      td {
        padding-top: 20px;
      }
    }

    .ant-table-thead > tr > th,
    .ant-table-tbody > tr > td {
      font-weight: 500;
      font-size: 12px;
      height: unset;
      padding: 16px 0;
      // color: rgba(255, 255, 255, 0.4);
      span {
        font-weight: 500;
        font-size: 12px;
        // color: rgba(255, 255, 255, 0.4);
      }
      &:first-child {
        padding-left: 20px;
      }
      &:last-child {
        padding-right: 20px;
      }
    }

    td {
      font-weight: 600;
      font-size: 14px;
      color: #fff;
    }

    .main-desc {
      line-height: 20px;
      margin-bottom: 2px;
    }

    .sub-desc {
      line-height: 20px;
      font-weight: 500;
      font-size: 12px;
      color: rgba(255, 255, 255, 0.6);
    }

    .new-switch {
      width: 24px;
      .switch-circle {
        width: 12px;
        height: 12px;
      }
      .switch-line {
        width: 24px;
        height: 6px;
        background: rgba(255, 255, 255, 0.1);
        border-radius: 10px;
        top: 3px;
      }
    }

    .j-token {
      margin-left: 16px;
      .token-names {
        font-weight: 600;
        font-size: 14px;
        line-height: 19px;
        color: #ffffff;
      }
      .description {
        font-weight: 500;
        font-size: 12px;
        line-height: 16px;
        color: rgba(255, 255, 255, 0.6);
      }
    }
    .j-supply {
      margin-right: 12px;
    }
    .fw500 {
      display: inline;
    }
    .pipe {
      width: 60px;
      height: 3px;
      margin-top: 6px;
      border-radius: 10px;
      background: rgba(255, 255, 255, 0.06);

      .active-bar {
        height: 3px;
        background: #9195fb;
        mix-blend-mode: normal;
        border-radius: 10px;
      }
    }

    .tooltip-icon {
      margin-right: 6px;
      width: 12px;
      height: 12px;
    }

    .ant-table-cell-content {
      overflow: visible !important;
    }

    tr > td {
      text-align: right;
      padding: 16px 0;
      &:nth-child(1) {
        text-align: left;
      }
    }
    .ant-table-thead {
      tr {
        th {
          height: unset;
          line-height: unset;
          text-align: right;
          padding: 16px 0;
          border-bottom: 1px solid rgba($color: #fff, $alpha: 0.06);

          &:nth-child(1) {
            text-align: left;
          }

          &:hover {
            background: #282931 !important;
          }
        }
      }
    }
  }

  body .sort-select-down {
    padding: 0 !important;
    border-radius: 6px !important;
    margin-top: 55px !important;
    overflow: visible !important;
    min-width: 160px !important;
    width: auto !important;
    &::after {
      content: '';
      display: block;
      width: 17.21px;
      height: 4px;
      background: url(../../images/mobile/triangle-top-white.png) 0 0 no-repeat;
      background-size: 100%;
      position: absolute;
      top: -3px;
      left: unset !important;
      right: 20%;
      transform: translateX(-50%);
    }
    .ant-select-item-option-content {
      text-align: center;
    }
    .ant-select-item-option-active,
    .ant-select-item-option-selected {
      background: rgba(61, 86, 214, 0.06) !important;
      color: #3d56d6 !important;
    }
    .ant-select-item {
      align-items: center;
      margin: 0 5px !important;
      border-radius: 4px;
      padding: 0;
      &:first-child {
        margin-top: 8px !important;
      }
      &:last-child {
        margin-bottom: 8px !important;
      }
    }
    .div-item {
      padding: 8px !important;
      text-align: left;
      &.active {
        background: transparent !important;
        color: #3d56d6 !important;
        font-weight: 400 !important;
        .long-arrow {
          background-image: url(../../images/mobile/arrow-long-active-icon.svg);
        }
      }
    }
    .long-arrow {
      display: inline-block;
      width: 14px;
      height: 10px;
      background: url(../../images/mobile/arrow-long-icon.svg) 0 0 no-repeat;
      background-size: 100%;
      margin: 0 4px;
    }
  }

  .market-container {
    &.j-market {
      padding: 0 15px;
    }
    .common-title {
      font-size: 16px;
    }
  }
  .j-market {
    .j-market-list {
      margin-top: 20px;
    }
  }
  .market-top {
    flex-direction: column;
    padding: 20px;
    & > div:first-child {
      font-size: 16px;
      text-shadow: 0px 2px 10px rgb(0 0 0 / 50%);
    }
  }
  .j-market {
    .j-announce {
      &.for-pc {
        display: none;
      }
      &.for-mobile {
        display: block;
        margin-top: 20px;
        padding: 15px;
        background: #282931;
        box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.1);
        border-radius: 2px;
      }
      .announce-icon {
        width: 12px;
        height: 12px;
        margin-top: 4px;
        margin-right: 10px;
      }
      .j-announce-top {
        display: flex;
        align-items: flex-start;
      }
      .j-announce-bottom {
        padding-left: 22px;
        margin-top: 10px;
      }
    }
  }

  .market-summary-list-v2 {
    .market-card {
      padding: 15px 0 0;
      margin-bottom: 10px;

      background: #282931;
      box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.1);
      border-radius: 2px;

      .card-top {
        display: flex;
        align-items: center;
        padding: 0 15px 15px;
        margin-bottom: 15px;
        border-bottom: 1px solid rgba(15, 19, 79, 0.05);
      }
      .card-bottom {
        padding: 0 15px;

        div:last-child {
          .cb-ele {
            padding-bottom: 10px;
            position: relative;
            margin-bottom: 15px;

            .cb-value {
              overflow: auto;

              .pipe {
                width: 60px;
                height: 3px;
                margin-top: 6px;
                background: rgba(20, 22, 49, 0.1);
                mix-blend-mode: normal;
                border-radius: 10px;
                display: flex;
                justify-content: flex-end;
                align-items: center;

                position: absolute;
                left: 0;
                top: auto;

                .active-bar {
                  height: 3px;
                  background: #9195fb;
                  mix-blend-mode: normal;
                  border-radius: 10px;
                }
              }
            }
          }
        }
      }
      .cb-ele {
        width: 60%;
        text-align: left;
        margin-bottom: 12px;
        &:nth-child(2) {
          width: 40%;
          margin-left: 20px;
        }
      }
      .mc-logo {
        width: 24px;
        height: 24px;
        margin-right: 8px;
      }
      .mc-symbol {
        display: inline-block;
        height: 24px;
        line-height: 24px;
        font-family: 'DM Sans-Medium';
        font-style: normal;
        font-weight: 500;
        font-size: 16px;
        color: #fff;
        margin-right: 4px;
      }
      .mc-subtitle {
        display: inline-block;
        height: 24px;
        line-height: 24px;
        font-family: 'DM Sans';
        font-style: normal;
        font-weight: 400;
        font-size: 12px;
        color: rgba(255, 255, 255, 0.6);
      }
      .ic-fire {
        margin-left: 4px;
      }
      .cb-title {
        font-style: normal;
        font-weight: 400;
        font-size: 12px;
        line-height: 17px;
        color: rgba(255, 255, 255, 0.6);
        margin-bottom: 5px;
        position: relative;
        span {
          font-size: 12px;
        }

        .tooltip-icon {
          margin-right: 6px;
          vertical-align: top;
          width: 12px;
          height: 12px;
          display: inline-block;
          background: url(../../images/v2/market/tooltip-icon.svg) no-repeat;
          background-size: 100%;
          position: absolute;
          left: -18px;
          top: 2px;
        }
      }
      .cb-value {
        display: inline-flex;
        align-items: center;
      }
      .cb-value,
      .cb-value .single {
        font-family: 'Rubik-Medium';
        font-style: normal;
        font-weight: 500;
        font-size: 16px;
        line-height: 19px;
        font-feature-settings: 'tnum' on, 'lnum' on;
        color: #fff;
        text-overflow: ellipsis;
        overflow: hidden;
        white-space: nowrap;
      }

      &.market-detail-card {
        .card-bottom {
          padding-bottom: 15px;
        }
      }
    }
  }

