@media screen and (max-width: 1000px) {
  #root .market-container .market-summary-list {
    tbody td,
    thead th {
      width: auto;
    }
    .tokenDetail {
      display: none;
    }
    .table-alike > div:nth-child(1) {
      width: 5%;
    }
  }

  #root .market-container .market-v2-summary-blocks .market-summary-block.reward-block {
    background-image: none;
    .reward-amount {
      font-size: 16px;
    }
  }

  .right-container {
    width: calc(100% - 230px);

    &.mobile-right-container {
      width: 100%;
      .market-summary-list {
        .tooltip-icon {
          position: relative;
          top: -1px;
          & ~ span {
            position: relative;
            top: 2px;
          }
        }
      }
      .ant-table-column-sorter {
        position: relative;
        top: -2px;
      }
    }
  }

  #root .j-wrapper .market-container .market-v2-summary-blocks {
    display: block;
    margin-top: 10px;

    .market-summary-block {
      width: 100%;
      padding: 0;
      margin-bottom: 10px;
      box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.05);

      &:first-child {
        margin-bottom: 10px;
      }

      &.lend-block {
        margin: 0 0 10px 0;
      }

      &.reward-block {
        width: unset;
        display: flex;
        padding: 15px;
        background-image: none;
        align-items: center;
        justify-content: space-between;
      }

      .market-summary-wrap {
        padding: 15px;
      }

      .mp-15 {
        padding: 15px;
      }

      .summary-footer {
        overflow: hidden;
        padding: 0;
        margin-top: 0;
        div {
          font-weight: 400;
          font-size: 12px;
          line-height: 17px;
          color: rgba(255, 255, 255, 0.6);
        }
        .num {
          font-family: 'Avenir Next';
          font-style: normal;
          font-weight: 600;
          font-size: 14px;
          line-height: 19px;
          color: #ffffff;
          margin-top: 8px;
        }
        &.second {
          margin-top: 0;
        }
      }

      .block-title {
        font-size: 14px;
        height: unset;
        line-height: unset;
      }

      .block-money {
        font-size: 16px;
        line-height: unset;
      }
    }
  }

  .j-wrapper .market-container {
    padding: 0 15px;
    margin-top: 7px;

    .market-info-block {
      display: block;
    }

    .market-detail-info,
    .echarts-frame {
      width: 100%;

      &:hover {
        .default-box {
          display: none;
        }
      }
    }

    .market-detail-info {
      margin-bottom: 20px;
    }

    .common-title::before {
      height: 13px;
      width: 5px;
      margin-right: 10px;
    }
  }
}

@media screen and (max-width: 750px) {
  .table-row > div > p {
    display: none;
  }

  .market-container .market-summary-list .table-alike.table-row > div:first-child > img {
    left: 13px;
  }

  .table-frame {
    overflow-x: scroll;
  }

  .market-container .market-summary-list .table-alike {
    min-width: 750px;
    &.table-row > div,
    &.table-header > div {
      text-overflow: ellipsis;
      // overflow: hidden;
      white-space: nowrap;
    }
  }

  .market-container .market-detail-header .header-info > div > p {
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
  }

  .market-container .echarts-frame .left {
    left: -1%;
  }
}

@media screen and (max-width: 800px) {
  .market-container .market-detail-header {
    display: block;
    height: auto;
    overflow: hidden;

    .header-title {
      width: 100%;
      display: block;
      margin-bottom: 20px;
    }

    .header-info {
      display: block;
      width: 100%;

      > div {
        float: left;
        width: 50%;

        &:nth-child(2n + 1) {
          text-align: left;
        }

        &:nth-child(3) {
          clear: left;
        }
      }
    }
  }

  .market-container {
    .market-footer {
      justify-content: space-between;

      .go-transcan {
        width: auto;
        padding-top: 5px;
      }
    }
  }
}

@media screen and (max-width: 500px) {
  .mltr {
    &.ant-tooltip {
      // max-width: 100%;
      // .ant-tooltip-arrow {
      //   left: unset;
      //   right: 34px;
      // }
    }
  }

  .market-container .market-summary-list .table-alike.table-row > div,
  .market-container .market-summary-list .table-alike.table-header > div,
  .market-container .market-detail-header .header-info > div > p {
    font-size: 12px;
  }

  .market-container .market-summary-list .table-alike.table-row {
    align-items: center;
    height: 45px;
    line-height: 45px;
  }

  .market-container .market-detail-header .header-title {
    // width: 10%;
    > img {
      width: 30px;
      height: 30px;
    }

    > span {
      font-size: 12px;
    }
  }

  // .market-container .market-detail-header .header-info {
  //   width: 85%;
  // }

  .market-container .market-summary-list .table-alike.table-row > div,
  .market-container .market-summary-list .table-alike.table-header > div {
    white-space: pre-wrap;
    text-overflow: unset;
    word-break: word-break;
    text-align: center;
    overflow: unset;
    height: auto;
    padding: 2px;
  }

  .market-container .market-summary-list .table-alike.table-header > div:first-child {
    text-align: center;
  }

  .market-container .market-summary-list .table-alike.table-header div {
    line-height: 16px;

    &:nth-child(3) {
      width: 25%;
    }
  }

  .market-container .market-summary-list .table-alike div.fw500 {
    line-height: 17px;
    word-break: break-all;
    font-size: 12px;

    span.fw500 {
      display: inline-block;
      font-size: 12px;
    }
  }

  .market-container .market-summary-list .table-alike.table-row {
    > div:first-child {
      height: 30px;
      text-align: center;
    }

    > div:nth-child(3) {
      width: 25%;
    }
  }

  .market-container .market-summary-list .table-alike.table-row > div:first-child > img {
    position: static;
    vertical-align: top;
    margin: 1px auto 0;
  }
}
