.j-container .j-infos {
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
}
.j-subhead .info .desc {
  text-indent: 16px;
}
@media screen and (max-width: 768px) {
  .j-container,
  .j-infos {
    display: flex;
    flex-direction: column;
  }
  .j-container {
    & > div {
      width: 100%;
    }
    .j-infos {
      // margin-top: 0;
    }
  }
}

@media screen and (min-width: 769px) and (max-width: 1340px) {
  .j-wrapper .ant-table-wrapper {
    overflow-x: auto;
  }
}

//Mobile Home styles Start
@media screen and (max-width: 798px) {
  .j-infos {
    display: flex;
    flex-direction: column;
    padding: 0 15px;
  }
}
//Mobile Home styles End
