.market-footer {
  background: #ffffff;
  box-shadow: 0px 10px 40px rgba(205, 205, 205, 0.1);
  border-radius: 8px;
  height: 146px;
  margin-top: 20px;

  .wallet-info {
    height: 61px;
    width: 100%;
    padding: 20px 20px 14px;
    border-bottom: 1px solid rgba(15, 19, 79, 0.05);

    .name {
      font-weight: 600;
      font-size: 14px;
      line-height: 26px;
      color: #0f134f;
    }

    button {
      width: 112px;
      height: 26px;
      background: rgba(61, 86, 214, 0.05);
      border: 1px solid #3d56d6;
      border-radius: 4px;
      font-weight: 400;
      font-size: 12px;
      line-height: 24px;
      text-align: center;
      color: #3d56d6;

      &:hover {
        background: #3d56d6;
        color: #fff;
      }

      &:nth-child(2) {
        background: rgba(24, 193, 159, 0.06);
        border: 1px solid #18c19f;
        border-radius: 4px;
        color: #18c19f;
        margin-left: 20px;
        &:hover {
          background: #18c19f;
          color: #fff;
        }
      }
    }
  }

  .wallet-detail {
    padding: 14px 20px 15px;
    span {
      font-style: normal;
      font-weight: 400;
      font-size: 12px;
      line-height: 17px;
      height: 17px;
      margin: 6px 0 5px;
      color: #545669;

      &:nth-child(2) {
        font-weight: 500;
        font-size: 14px;
        color: #0f134f;
      }
      .font-rubik-medium {
        font-size: 14px;
        color: #0f134f;
