.market-action-pop {
  .ant-popover-content {
    width: 202px;
    height: 88px;
    .flex-between {
      padding: 8px 10px;
      font-family: 'DM Sans';
      font-style: normal;
      font-weight: 400;
      font-size: 14px;
      line-height: 20px;
      border-radius: 4px;
      img {
        width: 20px;
        height: 20px;
        vertical-align: top;
        margin-right: 10px;
      }
      .toTronscan {
        width: 12px;
        height: 12px;
        background: url(../../images/toTronscan.svg) no-repeat center;
        display: inline-block;
        position: relative;
        top: 1px;
        background-size: 100%;
        &.disabled {
          background: url(../../images/toTronscanDisabled.svg) no-repeat center;
          background-size: 100%;
          cursor: default;
        }
      }
      .addtoTronLink {
        width: 16px;
        height: 13px;
        background: url(../../images/mobile/to-tronlink-icon.png) no-repeat 0 0;
        background-size: cover;
        display: inline-block;
        margin-left: 13px;
        cursor: pointer;

        &.disabled {
          background: url(../../images/mobile/to-tronlink-icon-disabled.png) no-repeat 0 0;
          background-size: cover;
          cursor: default;
        }
      }

      &:hover {
        background: rgba(61, 86, 214, 0.06);
      }
    }
  }
  .ant-popover-inner-content {
    padding: 8px 6px;
  }
}

